import { Component, OnInit, Input } from '@angular/core';

import { RequestService } from '../request.service';

import { FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-display',
  templateUrl: './display.component.html',
  styleUrls: ['./display.component.css']
})
export class DisplayComponent implements OnInit {

  private pokeList;
  checkoutForm;
  constructor(private req: RequestService, private formBuilder: FormBuilder) {
    this.checkoutForm = this.formBuilder.group({
      search: ''
    });
   }

  ngOnInit(): void {
    //this.displaySearch();
  }

  displaySearch() {
    console.log("in search functino of display comp")
    this.req.searchDB().subscribe((v) => {
      console.log(v)})
  }

  displaySearchByName(name: string){
    this.req.searchDBByName(name).subscribe((v) => {
      this.pokeList = v})
  }

  //onclick event to eecute displaysearchbyname
  onSubmit(inputData){
    this.checkoutForm.reset();
  }


}
